.range{
    width:auto;
}
.range--description{
    display:flex;
    justify-content: space-between;
    margin-bottom:18px;
    line-height: 15px;
}
.range--description-text{
    font-family: Montserrat-Bold;
    text-transform: uppercase;
}
.range--description-values{
    display:flex;
    font-family: 'Montserrat';
    font-size:12px;
}
.slider--base{
    position:relative;
    width:100%;
    height:6px;
    @include inputBorder();
    border-radius:3px;
    display:flex;
    align-items:center;
    justify-content: center;
    z-index: 0;
}
.slider--base-hover{
    position:absolute;
    z-index: 0;
    width: 34%;
    left: 75px;
    height:6px;
    background: linear-gradient(180deg, $green 0%, #66D2EA 100%);
    border-radius:3px;
    cursor:pointer;
}
.slider--left-btn, .slider--right-btn{
    position: absolute;
    z-index: 2;
    width:14px;
    height:14px;
    border:2px solid #fff;
    background: linear-gradient(180deg, $green 0%, #66D2EA 100%);
    border-radius: 50%;
    cursor:pointer;
}
.slider--left-btn{
    left:75px;
    cursor:pointer;
}
.slider--right-btn{
    right:90px;
    @media screen and (max-width:1199px) and (min-width:900px){
        right:45px;
    }
}